import React from 'react';
import { Link } from 'react-router-dom';

class Nav extends React.Component {
    render() {
        return (
            <nav className="navbar navbar-expand-lg navbar-dark justify-content-between">

                <Link className="navbar-brand" to="#">OpenLibrary</Link>

                <div className="navbar-nav">
                    <Link className="nav-item nav-link">Home</Link>
                    <Link className="nav-item nav-link">About</Link>
                    <Link className="nav-item nav-link">Contact</Link>
                </div>

            </nav>
        );
    }
}

export default Nav;